  @media (min-width: 420px) {
    .mobile-menu {
      display: none; } }

.mostrar {
  display: contents;
  visibility: visible;
  opacity: 1;
  height: auto; }

.bg-menu {
  z-index: 2;
  height: fit-content;
  content: "";
  position: absolute;
  border-radius: 1rem;
