  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  height: 0; }
  @media (min-width: 420px) {
    .navegacion {
      display: flex;
      align-items: center;
      opacity: 1;
      visibility: visible; } }
  .navegacion a {
    display: block;
    text-align: center;
    font-weight: 600;
    max-width: 100%;
    color: #818498;
    padding: 1.2rem 3rem;
    margin: 0.5rem 0;
    border-radius: 3rem; }
    @media (min-width: 420px) {
      .navegacion a {
        display: flex;
        padding: 1.1rem 2.5rem;
        border-radius: 3rem;
        color: white; } }
    .navegacion a:hover {
      border: #23303e solid 2px;
      opacity: 0.7; }
      @media (min-width: 420px) {
        .navegacion a:hover {
          background-color: rgba(255, 255, 255, 0.2);
          border: none;
          opacity: 1; } }
    .navegacion a:last-of-type {
      background-color: #fad400;
      color: #23303e;
      text-transform: uppercase;
      font-family: "Abril Fatface", cursive;
      margin-bottom: 2rem; }
      @media (min-width: 420px) {
