          background-color: white;
          margin: 0; } }
    .navegacion a:last-of-type:hover {
      background-color: #23303e;
      opacity: 1;
      color: #fad400;
      border: none; }
      @media (min-width: 420px) {
        .navegacion a:last-of-type:hover {
          background-color: rgba(255, 255, 255, 0.2);
          color: white; } }

.navegacion-footer {
  display: flex;
