    .about-grid .audience {
      padding: 12rem 12rem;
      text-align: start; } }
  .about-grid .brand p,
  .about-grid .audience p {
    color: #818498; }

.about-grid .hover-y {
  position: relative;
  z-index: 2;
  padding: 0.1rem 0.7rem; }
  .about-grid .hover-y::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 35%;
    border-radius: 0.3rem;
    opacity: 0.3;
    background-color: #fad400;
    transition: all 0.2s ease 0s;
    z-index: 1; }
  .about-grid .hover-y:hover::before {
    height: 35%;
    border-radius: 0.3rem;
    opacity: 0.7; }

.about-grid .hover-p {
  position: relative;
  z-index: 2;
  padding: 0.1rem 0.7rem; }
  .about-grid .hover-p::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 35%;
    border-radius: 0.3rem;
    opacity: 0.3;
    background-color: #fe7867;
    transition: all 0.2s ease 0s;
    z-index: 1; }
  .about-grid .hover-p:hover::before {
    height: 35%;
    border-radius: 0.3rem;
    opacity: 0.7; }

.design-img,
